.header-asserts {
  padding: 0 !important;
  min-width: 150px;
}

.header-content {
  min-width: 150px;
}

.menu-list {
  margin-top: 70px;
  padding: 0;
}

.menu-list li {
  list-style-type: none;
  padding: 20px 20px;
  width: 130px;
}

.active {
  color: greenyellow;
}

.menu-list li a {
  font-size: 18px;
  color: white;
  font-family: 'Staatliches';
  text-decoration: none;
}

.menu-list-icon li {
  list-style-type: none;
  margin: 20px 0;
}

.menu-list-icon li a {
  color: rgb(165, 241, 50);
  font-size: 3em;
  text-decoration: none;
}

.menu-list-icon li a:hover,
.menu-list li a:hover {
  color: rgb(165, 241, 50);
}

.logo-img {
  width: 150px;
  padding: 50px 25px;
  margin: 0 20px 0 50px;
}

@media (max-width: 1100px) {
  .header-content {
    justify-content: space-between;
  }

  .logo-img {
    margin: 50px 75px;
  }

  .menu-list-icon {
    margin: 50px 75px;  
    z-index: 30;
  }

  .menu-list {
    display: flex;
    flex-direction: column;
    background: rgb(88, 87, 87);
    width: 400px;
    height: 100%;
    position: absolute;
    right: 0;
    align-items: center;
    z-index: 10;
    margin: 0;
    padding-top: 300px;
  }

  .menu-list li {
    width: 100%;
    padding: 0;
  }

  .menu-list li a {
    display: block;
    width: 100%;
    padding: 10px;
    color: rgb(165, 241, 50);
  }

  .toggle-menu {
    display: none !important;
  }

  .ham {
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    transition: transform 400ms;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  .ham6 .top {
    stroke-dasharray: 40 172;
  }

  .ham6 .middle {
    stroke-dasharray: 40 111;
  }

  .ham6 .bottom {
    stroke-dasharray: 40 172;
  }

  .ham6.active .top {
    stroke-dashoffset: -132px;
  }

  .ham6.active .middle {
    stroke-dashoffset: -71px;
  }

  .ham6.active .bottom {
    stroke-dashoffset: -132px;
  }

  .line {
    fill: none;
    transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
    stroke: rgb(165, 241, 50);
    stroke-width: 5.5;
    stroke-linecap: round;
  }
}

@media (max-width: 900px) {
