body {
  background-color: rgb(221, 221, 223);
  background-size: cover;
  min-width: 600px;
}

.title {
  color: white;
  font-family: 'Monoton';
  font-size: 40px;
  font-weight: 550;
  text-shadow: 2px 2px 5px black;
}

.subtitle {
  color: rgb(165, 241, 50);
  font-family: 'Lekton';
  font-size: 22px;
  font-weight: 600;
  text-shadow: 2px 2px 5px black;
}

.phrase0 {
  color: white;
  font-family: 'Lekton';
  font-size: 18px;
  font-weight: 700;
  text-shadow: 2px 2px 5px black;
}

@media (min-width: 1100px) {
  .desktop-hide {
    display: none;
  }
}

@media (max-width: 1100px) {
  .mobile-hide {
    display: none;
  }
  
  .phrase0, .subtitle, .title {
    text-align: center;
    padding: 10px;
  }
}

@media (max-width: 900px) {
  .title {
    font-size: 40px;
  }
  
  .subtitle {
    font-size: 22px;
  }
  
  .phrase0 {
    font-size: 18px;
  }
}

@media (max-width: 700px) {

}